* {
  box-sizing: border-box;
}

.loader {
  height: 32px;
  width: 120px;
  box-sizing: border-box;
  transform-origin: 50% 100%;
  border-bottom: 5px solid #fff;
  animation: balancing 2s linear infinite alternate;
  display: block;
  position: relative;
}

.loader:before {
  content: "";
  width: 26px;
  height: 26px;
  background: #ff3d00;
  border-radius: 50%;
  animation: ballbns 2s linear infinite alternate;
  position: absolute;
  bottom: 0;
  left: 0;
}

.loader:after {
  content: "";
  height: 20px;
  width: 20px;
  border: 6px solid #fff;
  border-radius: 50%;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 100%);
}

@keyframes ballbns {
  0% {
    left: 0;
    transform: translateX(0%);
  }

  100% {
    left: 100%;
    transform: translateX(-100%);
  }
}

@keyframes balancing {
  0% {
    transform: rotate(-15deg);
  }

  50% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(15deg);
  }
}

body {
  margin: 0, 20;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(-45deg, rgba(176, 238, 82, .047), #ee7752, rgba(189, 119, 146, .463), rgba(82, 238, 116, .48), #0c81ab, #af8578, #8258aa) 0 0 / 400% 400%;
  padding-left: 20px;
  padding-right: 20px;
  animation: gradient 15s infinite;
}

@keyframes gradient {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

